import { classMap } from 'lit/directives/class-map.js';
import { html } from 'lit';
import { ifDefined } from 'lit/directives/if-defined.js';
import { property, query, state } from 'lit/decorators.js';
import { watch } from '../../internal/watch';
import componentStyles from '../../styles/component.styles';
import Icon from '../icon/icon.component';
import styles from './link.styles';
import UIKitElement from '../../internal/uikit-element';
import type { CSSResultGroup } from 'lit';

/**
 * @summary Links are used as navigational elements.
 * @documentation https://kind-desert-0fb80550f.5.azurestaticapps.net
 *
 * @dependency koerber-icon
 *
 * @event koerber-blur - Emitted when the link loses focus.
 * @event koerber-focus - Emitted when the link gains focus.
 *
 * @slot - The link's label.
 *
 * @csspart base - The component's base wrapper.
 * @csspart prefix - The container that wraps the prefix.
 * @csspart label - The button's label.
 * @csspart suffix - The container that wraps the suffix.
 * @csspart caret - The button's caret icon, an `<koerber-icon>` element.
 */

export default class Link extends UIKitElement {
  static styles: CSSResultGroup = [componentStyles, styles];
  static dependencies = {
    'koerber-icon': Icon
  };

  @property({ type: String, reflect: true }) href = '';

  /** Tells the browser where to open the link. Only used when `href` is set. */
  @property({ reflect: true }) target = '_blank';

  /** The `rel` attribute to use on the link. Only used when `href` is set. */
  @property() rel = 'noreferrer noopener';

  @property({ type: String, reflect: true }) title = '';

  /** The link's size. */
  @property({ reflect: true }) size: 'small' | 'medium' | 'large' = 'medium';

  /** Disables the link. */
  @property({ type: Boolean, reflect: true }) disabled = false;

  @property({ type: Boolean, reflect: true }) underline = false;

  /** Shows the Icon */
  @property({ type: Boolean, reflect: true }) showIcon = false;

  @query('.link') link: HTMLLinkElement;

  @state() private hasFocus = false;

  private handleBlur() {
    this.hasFocus = false;
    this.emit('koerber-blur');
  }

  private handleFocus() {
    this.hasFocus = true;
    this.emit('koerber-focus');
  }

  connectedCallback() {
    super.connectedCallback();
    this.addEventListener('click', this.handleHostClick);
  }

  disconnectedCallback() {
    super.disconnectedCallback();
    this.removeEventListener('click', this.handleHostClick);
  }

  private handleHostClick = (event: MouseEvent) => {
    // Prevent the click event from being emitted when the link is disabled
    if (this.disabled) {
      event.preventDefault();
      event.stopImmediatePropagation();
    }
  };

  @watch('disabled')
  handleDisabledChange() {
    this.setAttribute('aria-disabled', this.disabled ? 'true' : 'false');
  }

  /** Simulates a click on the link. */
  click() {
    this.link.click();
  }

  /** Sets focus on the link. */
  focus(options?: FocusOptions) {
    this.link.focus(options);
  }

  /** Removes focus from the link. */
  blur() {
    this.link.blur();
  }

  render() {
    let iconContent;
    if (this.showIcon) {
      iconContent = html` <div part="icon" class="link__icon" aria-hidden="true">
        <koerber-icon name="arrow-down" size=${this.size}></koerber-icon>
      </div>`;
    }
    return html`
      <a
        id="link"
        part="label"
        class=${classMap({
          link: true,
          'link--small': this.size === 'small',
          'link--medium': this.size === 'medium',
          'link--large': this.size === 'large',
          'link--disabled': this.disabled,
          'link--underline': this.underline,
          'link--focused': this.hasFocus
        })}
        ?disabled=${this.disabled}
        role=${ifDefined('link')}
        aria-disabled=${this.disabled ? 'true' : 'false'}
        tabindex=${this.disabled ? '-1' : '0'}
        @blur=${this.handleBlur}
        @focus=${this.handleFocus}
        @click=${this.disabled ? undefined : this.handleHostClick}
        href="${this.href}"
        target="${ifDefined(this.target ? this.target : undefined)}"
        rel=${ifDefined(this.target ? this.rel : undefined)}
        title=${this.title}
      >
        <slot></slot>
        ${iconContent}
      </a>
    `;
  }
}
